@import "https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap";
:root {
  --content-width: 1216px;
  --container-offset: 16px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --font-family-primary: "Inter", sans-serif;
  --fw-300: 300;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --fw-700: 700;
  --fw-800: 800;
  --color-white: #fff;
  --color-violet-100: #f0e9e1;
  --color-violet-50: #f2f0ec;
  --color-green-400: #1c8026;
  --color-dark-800: #333;
  --color-dark-900: #2e2e2e;
  --color-grey-10: #eceef2;
  --radius-main: 8px;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

html, body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

.page {
  height: var(--vh);
  font-family: var(--font-family-primary);
  font-weight: var(--fw-500);
  letter-spacing: 0;
  color: var(--color-dark-800);
  background-color: var(--color-violet-100);
  font-optical-sizing: auto;
  scroll-padding: var(--header-height);
  font-size: clamp(16px, 1vw, 20px);
  font-style: normal;
  line-height: 1.5;
}

.page__body {
  min-width: 320px;
  min-height: var(--vh);
  position: relative;
}

.page__body:before {
  content: "";
  z-index: -1;
  opacity: 0;
  background-color: #27282bbf;
  transition: opacity .3s;
  position: absolute;
  inset: 0;
}

.page.open {
  overflow: hidden;
}

.page.open > .page__body:before {
  z-index: 5;
  opacity: 1;
}

.section {
  padding: 7rem 0;
}

@media only screen and (max-width: 1180px) {
  .section {
    padding: 60px 0;
  }
}

@media only screen and (max-width: 992px) {
  .section {
    padding: 30px 0;
  }
}

.container {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.site-container {
  min-height: var(--vh);
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.main {
  padding-top: var(--header-height);
}

.logo {
  width: 100%;
  max-width: 210px;
  margin-right: 9.375rem;
}

@media only screen and (max-width: 1180px) {
  .logo {
    margin-right: 16px;
  }
}

.hidden {
  opacity: 0;
}

.sr-only {
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  width: 1px !important;
  height: 1px !important;
  white-space: nowrap !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  border: 0 !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.dis-scroll {
  width: 100%;
  height: 100dvh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.js-focus-visible:focus:not(.focus-visible) {
  outline: none;
}

h1, h2, h3 {
  font-weight: var(--fw-500);
  margin-bottom: 16px;
  font-size: clamp(1.875rem, 1.3333rem + 2.4074vi, 3.5rem);
  line-height: 1.21;
  overflow: hidden;
}

h2 {
  font-weight: var(--fw-600);
  margin-bottom: 16px;
  font-size: clamp(1.5rem, 1rem + 2.2222vi, 3rem);
  line-height: 1.2;
}

h3 {
  font-weight: var(--fw-600);
  margin-bottom: 9px;
  font-size: 24px;
  line-height: 1.2;
}

p {
  opacity: .6;
  font-size: 16px;
}

.subtext {
  opacity: 1;
  font-size: clamp(1rem, .9167rem + .3704vi, 1.25rem);
}

.header {
  z-index: 10;
  width: 100%;
  background-color: var(--color-violet-100);
  padding: 23px 0 31px;
  transition-property: transform;
  transition-duration: .15s;
  position: fixed;
  top: 0;
}

.header.hide {
  transform: translateY(-200%);
}

@media only screen and (max-width: 992px) {
  .header {
    padding: 15px 0;
  }
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.nav__inner {
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.nav__list {
  align-items: center;
  margin-right: 36px;
  display: flex;
}

.nav__item:not(:last-of-type) {
  margin-right: 32px;
}

@media only screen and (max-width: 1180px) {
  .nav__item:not(:last-of-type) {
    margin-right: 15px;
  }
}

.nav__link {
  white-space: nowrap;
  transition: color .3s linear text-shadow .3s linear;
  font-size: 16px;
  line-height: 1.18;
}

.nav__link:hover {
  text-shadow: .4px .4px;
  color: var(--color-dark-900);
}

.nav__link.true {
  font-weight: var(--fw-800);
  color: var(--color-dark-900);
}

.nav__btns {
  align-items: center;
  gap: 8px;
  display: flex;
}

@media only screen and (max-width: 1180px) {
  .nav__list {
    margin-right: 16px;
  }
}

@media only screen and (max-width: 992px) {
  .nav__inner {
    z-index: -1;
    padding-top: calc(var(--header-height) * 1.3);
    width: 100%;
    height: var(--vh);
    max-width: 350px;
    background-color: var(--color-violet-100);
    flex-direction: column;
    justify-content: flex-start;
    transition-property: transform;
    transition-duration: .4s;
    position: fixed;
    top: 0;
    right: 0;
    overflow-y: auto;
    transform: translateX(150%);
  }

  .nav .nav__list {
    z-index: -1;
    flex-direction: column;
  }

  .nav .nav__item {
    margin-bottom: 30px;
  }

  .nav .nav__item:not(:last-child) {
    margin-right: 0;
  }

  .nav .nav__link {
    font-size: 16px;
  }
}

[class].btn {
  border-radius: calc(var(--radius-main) * 4);
  width: 100%;
  height: max-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  white-space: nowrap;
  font-weight: var(--fw-600);
  color: var(--color-white);
  background-color: var(--color-green-400);
  border: 1px solid #0000;
  justify-content: center;
  align-items: center;
  padding: 15px 45px;
  font-size: 16px;
  line-height: 1;
  transition-property: background-color, color, border-color;
  transition-duration: .3s;
  display: flex;
}

[class].btn:hover, [class].btn:focus {
  border-color: var(--color-green-400);
  color: var(--color-green-400);
  background-color: var(--color-white);
}

[class].btn.formsapp-button {
  margin: 0;
  font-family: var(--font-family-primary) !important;
}

[class].btn--reverse {
  border-color: var(--color-green-400);
  font-weight: var(--fw-400);
  color: var(--color-green-400);
  background-color: var(--color-white);
  padding: 13px 32px;
  line-height: 1.11;
}

[class].btn--reverse:hover, [class].btn--reverse:focus {
  color: var(--color-white);
  background-color: var(--color-green-400);
}

[class].btn--article {
  border-color: var(--color-dark-800);
  max-width: 100%;
  color: var(--color-dark-800);
  background-color: #0000;
  padding: 15px 24px;
}

[class].btn--request {
  padding: 8px 24px;
  color: var(--color-white) !important;
  background-color: var(--color-green-400) !important;
}

[class].btn--request:hover, [class].btn--request:focus {
  border-color: var(--color-green-400);
  color: var(--color-green-400) !important;
  background-color: var(--color-white) !important;
}

[class].btn--sign {
  border-color: var(--color-dark-800);
  color: var(--color-dark-800);
  background-color: var(--color-white);
  padding: 8px 24px;
}

[class].btn--sign:hover, [class].btn--sign:focus {
  color: var(--color-white);
  background-color: var(--color-green-400);
}

[class].btn--slider {
  z-index: 2;
  height: 32px;
  max-width: 32px;
  background-color: #0000;
  border-color: #0000;
  padding: 0;
  position: relative;
}

[class].btn--slider:before {
  content: "";
  width: 32px;
  height: 32px;
  background-color: currentColor;
  background-color: var(--color-dark-800);
  transition: background-color .4s linear;
  position: absolute;
  -webkit-mask-image: url("../images/icons/arrow.svg");
  mask-image: url("../images/icons/arrow.svg");
  -webkit-mask-position: 0 0;
  mask-position: 0 0;
  -webkit-mask-size: cover;
  mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
}

[class].btn--slider:hover, [class].btn--slider:focus {
  background-color: #0000;
  border-color: #0000;
}

[class].btn--slider:hover:before, [class].btn--slider:focus:before {
  opacity: 1;
}

.burger {
  --line-height: 2px;
  --burger-size: 20px;
  --move: calc(var(--burger-size) / 3);
  --move-decrement: calc(var(--move) * -1);
  --transition-time: .5s;
  --lines-color: var(--color-dark-800);
  width: calc(var(--burger-size)  + 5px);
  height: var(--burger-size);
  transition: opacity var(--transition-time) ease;
  background-color: #0000;
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.burger span {
  z-index: -1;
  width: 100%;
  height: var(--line-height);
  background-color: var(--lines-color);
  transition: transform var(--transition-time) ease, background-color var(--transition-time) ease .3s;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger span:before, .burger span:after {
  content: "";
  width: 100%;
  height: var(--line-height);
  background-color: var(--lines-color);
  animation-duration: .4s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
  position: absolute;
  left: 0;
}

.burger span:before {
  transform: translateY(var(--move-decrement)) rotate(0);
}

.burger span:after {
  transform: translateY(var(--move)) rotate(0);
}

.burger:hover {
  opacity: 1;
}

@media only screen and (max-width: 992px) {
  .burger {
    display: block;
  }
}

.closed.opened span {
  background-color: var(--lines-color);
  transition: background-color .2s;
}

.closed.opened span:before {
  animation-name: toplineburger;
}

.closed.opened span:after {
  animation-name: bottomlineburger;
}

.opened .nav__inner {
  transform: translateX(0);
}

.opened span {
  background-color: #0000;
  transition: background-color .2s;
}

.opened span:before, .opened span:after {
  animation-duration: .2s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
}

.opened span:before {
  animation-name: toplinecross;
}

.opened span:after {
  animation-name: bottomlinecross;
}

@keyframes toplinecross {
  0% {
    transform: translateY(var(--move-decrement));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes bottomlinecross {
  0% {
    transform: translateY(var(--move));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(0)rotate(-45deg);
  }
}

@keyframes toplineburger {
  0% {
    transform: rotate(45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move-decrement)) rotate(0);
  }
}

@keyframes bottomlineburger {
  0% {
    transform: translateY(0)rotate(-45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move)) rotate(0);
  }
}

